.video {
  &__box {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    margin-bottom: 2.5rem;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    :first-child {
      font-size: 2rem;
    }

    :last-child {
      font-size: $default-font-size;
    }

    :nth-of-type(n) {
      margin-bottom: 1rem;
    }
  }

  &__commentItem {
    display: flex;
    padding: 1rem;

    &-avatar {
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      margin-right: 1.5rem;
    }

    &-title {
      display: flex;

      h3 {
        margin-right: 1rem;
        margin-bottom: 0.5rem;
        font-size: 1.3rem;
      }

      span {
        color: #606060;
        font-size: 1.2rem;
      }
    }

    &-comment {
      font-size: 1.4rem;
    }
  }

  &__list {
    border: 1px solid #ccc;
    overflow-y: auto;
    max-height: 56.6rem;

    &::-webkit-scrollbar {
      width: 1rem;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #7f7f7f;
      border: 1px solid transparent;
      background-clip: content-box;
      border-radius: 8px;

      &:hover {
        background-color: #a5a5a5;
      }
    }

    @include respond(tab-land) {
      padding: 0;
      border: none;
      overflow: unset;
      max-height: none;
    }
  }

  &__item {
    padding: 1rem;
    display: flex;
    align-items: flex-start;
    cursor: pointer;

    img {
      max-width: 180px;
    }

    &-text {
      padding: 1rem;
      font-size: 1.2rem;
    }

    &:hover {
      background-color: #e5e6e4;
      box-shadow: 0 2px 4px #606060;
      @include respond(phone) {
        background-color: #fff;
        box-shadow: none;
      }
    }

    &:active {
      @include respond(phone) {
        background-color: #e5e6e4;
      }
    }
  }
}
